/* purgecss start ignore */

.switch {
  position: relative;
  display: inline-block;
  width: 300px;
  height: 40px;
}

.switch input{
  opacity: 0;
  width: 0;
  height: 0;
}

.slider{
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #111827;
  border-radius: 5px;
  transition: 0.4s;
}

.slider:before{
  position: absolute;
  content: "Pago Semanal" ;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 150px;
  top: 4px;
  left: 4px;
  background-color: #F57C00;
  border-radius: 5px;
  transition: 0.4s;
}

input:checked + .slider::before {
  -webkit-transform: translateX(142px);
  transform: translateX(142px);
  content: "Pago Anual" ;
  justify-content: center;
  align-items: center;
}

.color{
  background-color: #E5E5E5;
}

